import { Component } from '@angular/core';

import { IShContextMenuItem, IShContextOptions } from 'ng2-right-click-menu';

// import { IShContextMenuItem } from '../../../context-menu/src/sh-context-item';
// import { IShContextOptions } from '../../../context-menu/src/sh-context-options';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'Right Click Me';
  titleRtl = 'Right Click Me (RTL)';
  items: IShContextMenuItem[];
  itemsRtl: IShContextMenuItem[];
  dataCtxOne: any;
  dataCtxTwo: any;
  options: IShContextOptions;

  constructor() {
    this.dataCtxOne = {
      One: 'One'
    };

    this.dataCtxTwo = {
      Two: 'Two'
    };

    this.items = [
      {
        label: 'Save Me On Your HD',
        onClick: this.clickEvent
      },
      {
        label: 'Edit',
        onClick: this.clickEvent
      },
      {
        label: 'Sub Menu',
        subMenu: true,
        subMenuItems: [
          {
            label: 'Save',
            onClick: this.clickEvent
          },
          {
            label: 'Edit',
            onClick: this.clickEvent
          },
          {
            label: 'Another Sub Menu',
            subMenu: true,
            subMenuItems: [
              {
                label: 'Save',
                onClick: this.clickEvent
              },
              {
                label: 'Edit',
                onClick: this.clickEvent
              }]
          }
        ]
      },
      {
        divider: true
      },
      {
        label: 'Remove',
        disabled: ctx => {
          return ctx.Two === 'Two';
        },
        onClick: this.clickEvent
      },
      {
        label: 'Hidden',
        onClick: this.clickEvent,
        visible: ctx => {
          return ctx.One === 'One';
        }
      }
    ];

    this.itemsRtl = [
      {
        label: 'שמור',
        onClick: this.clickEvent
      },
      {
        label: 'ערוך',
        onClick: this.clickEvent
      },
      {
        label: 'תפריט נוסף',
        subMenu: true,
        subMenuItems: [
          {
            label: 'שמור',
            onClick: this.clickEvent
          },
          {
            label: 'ערוך',
            onClick: this.clickEvent
          },
          {
            label: 'עוד תפריט נוסף',
            subMenu: true,
            subMenuItems: [
              {
                label: 'שמור',
                onClick: this.clickEvent
              },
              {
                label: 'ערוך',
                onClick: this.clickEvent
              }]
          }
        ]
      }
    ];


    this.options = {
      rtl: true
    };
  }

  clickEvent($event: any) {
    console.log('clicked ', $event);
  };
}



